.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grid{
  margin: 2rem 10rem;
  display: grid;
  justify-content: center;
  gap: 0;
  grid-template-columns: repeat(9,90px);
  grid-template-rows: repeat(9,75px);
}

.num{
  border: solid black 1px;
  text-align: center;
  font-size: 25px;
}

.head{
  border: gray solid 1px;
  border-radius: 8px;
  position: fixed;
  /* display: flex;
  justify-content: space-around; */
  top: 40px;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 40%;
  background-color: white;
  padding: 10px;
}


.input{
  display: flex;
  justify-content: space-around;
}

.details{
  display: flex;
  align-items: center;
  padding-top: 3px;
  justify-content: space-between;
  padding-left: 35px;
  /* gap: 110px; */
  /* padding-right: 190px; */
}

input{
  border: solid black 1px;
  border-radius: 4px;
  padding: 2px;
  padding-left: 4px;
}

button{
  background-color: rgb(255,193,7);
  border: none;
  padding: 6px;
  border-radius: 4px;
}

button:hover{
  cursor: pointer;
  opacity: 0.8;
  transition-delay: 0.1s;
}

.details > button{
  padding-left: 7px;
  padding-right: 7px;
  margin-left: 25px;
}

.x{
  position: relative;
  left: 90px;
  display: none;
}
.r{
  position: relative;
  right: 50px;
}